import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import '@vkontakte/vkui/dist/vkui.css';

import Splash from './panels/Splash';
import ModalSelectors from './panels/ModalSelectors';
import Home from './panels/Home';
import Persik from './panels/Persik';

const App = () => {

 const [activePanel, setActivePanel] = useState('splash');
 const [fetchedUser, setUser] = useState(null);
 const [popout, setPopout] = useState(null);
 const [modal, updateModal] = useState(null);
 const [modalHistory, updateModalHistory] = useState([]);

	useEffect(() => {
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await connect.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		if(typeof e != 'string')
			setActivePanel(e.currentTarget.dataset.to);
		setActivePanel(e);
	};

	const setOneModal = (value) => {
		value = value || null;
		let currModalHistory = modalHistory ? [...modalHistory] : [];
		if(value === null) {
			currModalHistory = [];
		} else if(currModalHistory.indexOf(value) !== -1) {
			currModalHistory = currModalHistory.splice(
				0,
				currModalHistory.indexOf(value) + 1);
		} else {
			currModalHistory.push(value);
		}
		updateModal(value);
		updateModalHistory(currModalHistory);
	}

	const modalBack = () => 
		setOneModal(modalHistory[modalHistory.length - 2]);

	const closeModal = () =>
		setOneModal(null);
	

	const setModalFrom = (e) => {
		const val = e.currentTarget.dataset.to;
		setOneModal(val);
	};

	return (
		<View activePanel={activePanel} popout={popout} modal={<ModalSelectors go={go} modal={modal} updateModal={setOneModal} modalBack={modalBack} closeModal={closeModal}/>}>
			<Splash id='splash' go={go}	setModal={setModalFrom}/>
			<Home id='home' fetchedUser={fetchedUser} go={go} />
			<Persik id='persik' go={go} />
		</View>
	);
}

export default App;

