import React, { useState } from 'react';
import connect from '@vkontakte/vk-connect';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Cookies from './cookies.js';
import {ModalRoot, ModalPage, ModalPageHeader, FormLayout, SelectMimicry, FormLayoutGroup, Radio, PanelHeaderBack} from '@vkontakte/vkui';

	const directionList = ['Программная инженерия', 'Бизнес-информатика'];
	const coursesList = ['1', '2', '3', '4', 'Магистратура-1', 'Магистратура-2'];
	const groupsList = ['ПрИ-101', 'ПрИ-102', 'ПрИ-201', 'БИ-101'];
	var cookies = new Cookies();


	const ModalGroupSelector = ({id, go, modal, updateModal, modalBack, closeModal}) => {
		const [directionValue, setDirectionValue] = useState(cookies.getCookie('direction'));
		const [courseValue, setCourseValue] = useState(cookies.getCookie('course'));
		const [groupValue, setGroupValue] = useState(cookies.getCookie('group'));

		function storeData() {
			cookies.setCookie('direction', directionValue);
			cookies.setCookie('course', courseValue);
			cookies.setCookie('group', groupValue);
			closeModal();
			go("persik");
		}

		return(
			<ModalRoot id={id} activeModal={modal}>
			<ModalPage
				id="groupSelectorPage"
				header={
					<ModalPageHeader
						left={<PanelHeaderBack onClick={() => modalBack()}/>}>
						Давайте всё настроим :)
					</ModalPageHeader>
				}
				onClose={
					() => closeModal()
				}>
				<FormLayout>
					<SelectMimicry top="Направление" placeholder="Выбрать направление" onClick={() => updateModal('direction')}>{directionValue}</SelectMimicry>
					<SelectMimicry top="Курс" placeholder="Выбрать курс" onClick={() => updateModal('course')}>{courseValue}</SelectMimicry>
					<SelectMimicry top="Группа" placeholder="Выбрать группу" onClick={() => updateModal('group')}>{groupValue}</SelectMimicry>
					<Button 
						size="xl" 
						onClick={() => storeData() } 
						>Погнали</Button>
				</FormLayout>
			</ModalPage>

			<ModalPage
				id="direction"
				header={
					<ModalPageHeader
						left={<PanelHeaderBack onClick={() => modalBack()}/>}>
						Выберите направление
					</ModalPageHeader> //direction
				}
				onClose={
					() => modalBack()
				}
			>
				<FormLayoutGroup>
					{
						directionList.map(
							(title, id) => {
								return (
									<Radio key={id} name="direction" value={id} onClick={()=> {setDirectionValue(title); modalBack();}}>{title}</Radio>
								);
							}
						)
					}
				</FormLayoutGroup>
			</ModalPage>

			<ModalPage
				id="course"
				header={
					<ModalPageHeader
						left={<PanelHeaderBack onClick={() => modalBack()}/>}>
						Выберите курс
					</ModalPageHeader> //course
				}
				onClose={
					() => modalBack()
				}
			>
				<FormLayoutGroup>
					{
						coursesList.map(
							(title, id) => {
								return (
									<Radio key={id} name="direction" value={id} onClick={()=> { setCourseValue(title); modalBack();}}>{title}</Radio>
								);
							}
						)
					}
				</FormLayoutGroup>
			</ModalPage>

			<ModalPage
				id="group"
				header={
					<ModalPageHeader
						left={<PanelHeaderBack onClick={() => modalBack()}/>}>
						Выберите группу
					</ModalPageHeader> //group
				}
				onClose={
					() => modalBack()
				}
			>
				<FormLayoutGroup>
					{
						groupsList.map(
							(title, id) => {
								return (
									<Radio key={id} name="direction" value={id} onClick={()=> { setGroupValue(title); modalBack();}}>{title}</Radio>
								);
							}
						)
					}
				</FormLayoutGroup>
			</ModalPage>
		</ModalRoot>
	);
}
export default ModalGroupSelector;